import React from 'react';
import Navigation from './Navigation/Navigation';
import './App.css';
import { BrowserRouter, Route } from 'react-router-dom';
import Weather from './weather/weather';

class App extends React.Component {

  render() {
    return (
      <BrowserRouter>
        <div className="App">
          <Navigation />
        </div>
        <Route path="/weather" > 
          <Weather />
        </Route>
      </BrowserRouter>
    );
  }
}

export default App;