import React from 'react';
import './Navigation.css';
import logo from '../assets/images/logo.png'
import { NavLink } from 'react-router-dom';

const Navigation = () => {
    return (
        <nav className="nav-bar">
            <ul className="nav-list">
                <li className="logo"><NavLink to="/"><img src={logo} alt="Logo" /></NavLink></li>
                <ul className="nav-list-sec">
                    <li><NavLink to="/">Home</NavLink></li>
                    <li><NavLink to="/">Help</NavLink></li>
                    <li><NavLink to="/">About</NavLink></li>
                    <li><NavLink to="/weather">Weather</NavLink></li>
                </ul>
                <li className="form">
                    <form action="">
                        <input className="input" type="text" name="search" placeholder="Search" />
                        <NavLink type="submit" to="/"><ion-icon name="search-outline"></ion-icon></NavLink>
                    </form>
                </li>
                <li className="account"><NavLink to="/"><ion-icon name="person-outline"></ion-icon></NavLink></li>
            </ul>
        </nav>
    );
}

export default Navigation;