import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './weather.css';

const Weather = () => {
    const [info, setState] = useState({ data: '' });
    const [temp, setTemp] = useState({ data: '' });
    const [city, setCity] = useState({data: ''})
    const [search, setSearch] = useState('');

    const getInfo = async () => {
        axios.get('http://localhost:8080/getweather?city=' + search)
            .then(({ data }) => {
                console.log(data[1]);
                setState({ data: data[0] });
                setTemp({ data: data[0].Temperature.Metric })
                setCity({data: data[1]})
            })
    }

    useEffect(() => {
        getInfo();

    }, [search])

    return (
        <React.Fragment >
            <div class="container my-5 mx-auto">
                <h1 class="text-muted do- text-center my-4">The Weather App</h1>
                <form onSubmit={e => {
                    e.preventDefault();
                    const sea = e.target[0].value;
                    setSearch(sea);

                }} class="change-location my-4 text-center text-muted">
                    <label for="city">Enter you city name</label>
                    <input type="text" name="city" class="form-control p-4" />
                    <button type="submit"></button>
                </form>
                <div class="card shadow-lg rounded">
                    <img src={info.IsDayTIme? "/img/day.svg" : "/img/night.svg"} class="time card-img-top" />
                    <div class="icon bg-light mx-auto text-center"><img src={`/img/icons/${info.data.WeatherIcon}.svg`} alt="" /></div>

                    <div>
                        <div class="text-muted text-uppercase text-center details">
                            <h5 class="my-3">{city.data.EnglishName? city.data.EnglishName : 'City Name'}</h5>
                            <div class="my-3">{info.data.WeatherText? info.data.WeatherText : 'Current Conditions'}</div>
                            <div class="display-4 my-4">
                                <span>{temp.data.Value? temp.data.Value : 'temp'}</span>
                                <span>&deg;C</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>

    )
}

export default Weather;